import ModulesNavComponent from '../modules-nav/modules-nav.vue'
export default {
   name: 'WorkshopComponent',
   components: {
      ModulesNavComponent
   },
   data: () => ({
      i18n: {
         card1: {
            timeline: {
               unit: '',
               title: ' '
            },
            last_update: ' ',
            start: ['', ''],
            level: {
               title: '',
               value: ['', '', '']
            }
         },
         card2: {
            users: '',
            about: '  ',
            resutlBtn: ' ',
            resutl: ' ',
            techniques_used: ' '
         },
         card3: {
            here: '',
            title: '  ',
            text: '                 '
         }
      },
      loaded: true
   }),

   props: ['workshop'],
   created() {

   },
   watch: {
      $route(to, from) {}
   },
   methods: {
      goToLessons(url) {
         this.$parent.$emit('clearTimeout')
         this.$router.push({
            name: 'modules',
            params: {
               module: 'collo',
               modules: this.workshop.modules
            }
         })
      }
   }
}



