export default {
  name: 'ContactUsComponent',
  components: {},
  data: () => ({
    forums_icon: '',
    forums_text: '               <a href="http://forums.coretabs.net" target="_blank"> </a>',
    heading_title_text: ' ',
    description_text: '            <a href="mailto:info@coretabs.com">info@coretabs.com</a>',
    heading_text: '  ',
    forgot_text: '        .',
    submit_btn_text: '',
    alert: {
      success: false,
      error: false,
      message: ''
    },
    valid: 0,
    vs: {
      v1: 0,
      v2: 0,
      v3: 0
    },
    fullname: '',
    fullname_label: ' ',
    fnRules: [
      v => !!v || '',
      v => (v && v.length <= 20) || '      20 '
    ],
    email: '',
    email_label: ' ',
    emRules: [
      v => !!v || '',
      v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || '   '
    ],
    message: '',
    message_label: '  ',
    meRules: [
      v => !!v || '',
      v => (v && v.length >= 10) || '  '
    ]
  }),
  created() {
    this.$store.dispatch('getImgUrl', 'icons/forums-logo.png').then(img => {
      this.forums_icon = img
    }).catch(error => {
        throw new Error(error.message)
    })
  },
  updated() {
    this.chackValid()
  },
  methods: {
    chackValid() {
      var root = this

      root.vs.v1 = 1
      root.vs.v2 = 1
      root.vs.v3 = 1

      root.fnRules.forEach((rule) => {
        if (rule(root.fullname) !== true) {
          root.vs.v1 = 0
        }
      })

      root.emRules.forEach((rule) => {
        if (rule(root.email) !== true) {
          root.vs.v2 = 0
        }
      })

      root.meRules.forEach((rule) => {
        if (rule(root.message) !== true) {
          root.vs.v3 = 0
        }
      })

      root.valid = root.vs.v1 + root.vs.v2 + root.vs.v3
    },
    submit() {
    }
  }
}



